  import axios from 'axios'
  import Card from '../../components/card'
  export default {
    data () {
      return {
        page:1,
        temp_data:{},
        data:{},
        API_PROXY : 'https://bird.ioliu.cn/v1/?url=',
        flag:true
      }
    },
    components: {
      Card
    },

    created(){
      this.getData(0,this.flag)
    },
    methods: {
      async getData(index, flag) {
        let url = ""
        url = this.API_PROXY+"https://m2.qiushibaike.com/article/newlist?new=0"
        this.temp_data=await axios.get(url)
        this.data= this.temp_data.data
        console.log(this.data)
        this.flag=false
      }
    }
  }
</script>

