import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home/home'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'//设置默认路由页为推荐页
    },
    {
      path: '/home',
      name:'home',
      component: home,
    },
    ]
})



